:root {
  --ration: 2;
}

.character-window {
  position: relative;

  width: calc(176px * var(--ration));
  height: calc(242px * var(--ration));

  background: grey;
  background-size: cover;
}

.character-window__control {
  position: absolute;

  width: calc(32px * var(--ration));
  height: calc(32px * var(--ration));
  bottom: calc(7px * var(--ration));

  border: none;
  outline: none;
  background-color: transparent;
  background-size: cover;

  cursor: pointer;
}

.character-window__control--left {
  left: calc(16px * var(--ration));
  background-color: blue;
}
.character-window__control--left:hover {
  background-color: red;
}

.character-window__control--right {
  right: calc(12px * var(--ration));
  background-color: blue;
}
