@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora&family=Montserrat&family=Roboto:wght@400;700&display=swap);
body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  padding: 0;
  margin: 0;
  background: #985ACE;
}

.App {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  font-family: sans-serif;
  text-align: center;
}
.signin-form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 200px;

  padding: 88px 92px 83px;

  border-radius: 30px;
  background: #FFFFFF;
}
.signin-form__title {
  margin-bottom: 44px;
  padding-left: 4px;

  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  color: #000000;
}
.signin-form__row {
  width: 100%;
  padding-bottom: 20px;
}
.signin-form__row:last-child {
  padding-bottom: 0;
}
.signin-form__field {
  width: 100%;
  height: 40px;
  padding: 0 16px;
  box-sizing: border-box;
  text-align: left;

  border-radius: 7px;
  background-color: #F8F1FF;
}

.signin-form__field-title {
  padding-top: 3px;
  text-align: left;

  color: rgba(64, 58, 75, 0.60);
  font-family: 'Lora', 'Times New Roman', Times, serif;
  font-size: 10px;
  font-weight: 400;
}

.signin-form__field--text {
  padding: 0;
  box-sizing: border-box;

  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  color: #403A4B;

  border: none;
  outline: none;
  background-color: transparent;
}

.signin-form__button {
  width: 100%;
  height: 40px;
  line-height: 40px;

  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  color: #FFFFFF;

  border: none;
  border-radius: 7px;
  background-color: #985ACE;

  cursor: pointer;
}

:root {
  --ration: 2;
}

.character-window {
  position: relative;

  width: calc(176px * 2);

  width: calc(176px * var(--ration));
  height: calc(242px * 2);
  height: calc(242px * var(--ration));

  background: grey;
  background-size: cover;
}

.character-window__control {
  position: absolute;

  width: calc(32px * 2);

  width: calc(32px * var(--ration));
  height: calc(32px * 2);
  height: calc(32px * var(--ration));
  bottom: calc(7px * 2);
  bottom: calc(7px * var(--ration));

  border: none;
  outline: none;
  background-color: transparent;
  background-size: cover;

  cursor: pointer;
}

.character-window__control--left {
  left: calc(16px * 2);
  left: calc(16px * var(--ration));
  background-color: blue;
}
.character-window__control--left:hover {
  background-color: red;
}

.character-window__control--right {
  right: calc(12px * 2);
  right: calc(12px * var(--ration));
  background-color: blue;
}

